.body{
    background-image: url("../img/bgimage.jpg");
    /*  background-color:blueviolet; */
      background-image:repeat;
}
#purple{
    box-shadow: 0 5px rgb(253, 142, 253);
    border-radius:95%;
    background-color:purple;
    float:left;
    color:silver;
    text-decoration:none; 
    padding:8px; 
    /*only work if .navbar button has a .hover*/
    transform: translateY(4px);
}

#purple:hover{
    color: black;
    background-color: grey;
}

 
main {
    margin:60px auto;
    border-style: inset;
    border-color: indigo;
    box-sizing: border-box;
    padding-top:20px;
    /* padding-top:5rem!important; */
   /*padding-right:20px;*/
    padding-bottom:20px;
    padding-left:20px;
    background-color:white;
    width: 60%;
    overflow-y:scroll;
}

#main_portfolio {
    width:85%;
}

main img {
    max-height:350px;
    max-width:350px;
}

article{
    width:90%;
    padding:1rem;
    /* font-size:1.5vw; */
    /* overflow:scroll; */
}

h1{
    text-decoration-color: goldenrod;
    text-decoration:double overline;
    font-size: 3vw;
    
}

/*for now, I like the professional look of only the overline*/
h1 span{
    text-decoration:double underline;
    text-decoration-color: indigo;
}


@media screen and (max-width:768px) {
    main{padding-top:5rem!important;
        }
    h1{
    font-size: 10vw;
        }
    }

